//*************
//* Animation *
//*************

//********
//* logo *
//********

@keyframes drawTopGuides {
  0% {
    transform: translate(-42px, 0px); }
  40% {
    transform: translate(40px, 0px); }
  60% {
    transform: translate(-20px, 0px); }
  100% {
    transform: translate(0px, 0px); } }
@keyframes drawLeftGuides {
  0% {
    transform: translate(0px, -43px); }
  40% {
    transform: translate(0px, 14px); }
  60% {
    transform: translate(0px, -12px); }
  100% {
    transform: translate(0px, 0px); } }
@keyframes drawBottomGuides {
  0% {
    transform: translate(44px, 0px); }
  40% {
    transform: translate(-30px, 0px); }
  60% {
    transform: translate(32px, 0px); }
  100% {
    transform: translate(0px, 0px); } }
@keyframes drawRightGuides {
  0% {
    transform: translate(0px, 45px); }
  40% {
    transform: translate(0px, -60px); }
  60% {
    transform: translate(0px, 12px); }
  100% {
    transform: translate(0px, 0px); } }
@keyframes quarter {
  0% {
    opacity: 0;
    transform: rotateZ(90deg); }
  40% {
    opacity: .8;
    transform: rotateZ(-12deg); }
  60% {
    opacity: .35;
    transform: rotateZ(12deg); }
  100% {
    opacity: 1;
    transform: rotateZ(0deg); } }
@keyframes half {
  0% {
    opacity: 0;
    transform: rotateZ(180deg); }
  40% {
    opacity: .8;
    transform: rotateZ(-10deg); }
  60% {
    opacity: .35;
    transform: rotateZ(10deg); }
  100% {
    opacity: 1;
    transform: rotateZ(0deg); } }
@keyframes three-quarter {
  0% {
    opacity: 0;
    transform: rotateZ(270deg); }
  40% {
    opacity: .8;
    transform: rotateZ(-5deg); }
  60% {
    opacity: .35;
    transform: rotateZ(20deg); }
  100% {
    opacity: 1;
    transform: rotateZ(0deg); } }
@keyframes full {
  0% {
    opacity: 0;
    transform: rotateZ(-360deg); }
  40% {
    opacity: .8;
    transform: rotateZ(-10deg); }
  60% {
    opacity: .35;
    transform: rotateZ(10deg); }
  100% {
    opacity: 1;
    transform: rotateZ(0deg); } }
//********
//* menu *
//********
@keyframes roll_in {
  0% {
    opacity: 0;
    transform: translate(-20px, 14px) scale(1) rotateZ(-90deg); }
  20% {
    opacity: 0.67;
    transform: translate(40px, 14px) scale(6) rotateZ(60deg); }
  80% {
    opacity: 1;
    width: 1px;
    height: 1px;
    color: hsla(0, 0%, 0%, 0);
    font-size: 0em;
    background-color: $accent;
    transform: translate(70px, 14px) scale(8) rotateZ(166deg); }
  100% {
    opacity: 1;
    width: 48px;
    height: 28px;
    color: $gray-40;
    font-size: 0.75em;
    background-color: $gray-12;
    transform: translate(0px, 0px) scale(1) rotateZ(0deg); } }
@keyframes falling_box {
  0% {
    top: -300px;
    height: 2px;
    transform: scale(6); }
  20% {
    top: 28px;
    height: 2px;
    transform: scale(.8); }
  80% {
    top: -10px;
    height: 2px;
    transform: scale(2); }
  100% {
    top: 0;
    height: 28px;
    transform: scale(1); } }
