/***********
 * common  *
 ***********/
html, body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  width: $width;
  height: $height; }
html {
  background: $gray-12; } // develpment delete
body {
  background-color: $gray-16;
  display: flex;
  flex-direction: row;
  nav {
   flex: 0 0 auto;
   order: 0; }
  main {
    flex: 0 0 auto;
    order: 1; } }
ul li {
  list-style: none; }
ul {
  padding: 0px;
  margin: 0px; }
a {
  display: block;
  text-decoration: none;
  color: $gray-40; }

